const WidgetLifecycle = ({ children }) => {
  // Receives children
  // const { handleDrop } = useDroppableArea();
  // const { showSuccessToast, showErrorToast } = useToast();
  // const initialDraggedData = useSelector((state) => {
  //   return state.droppedObject.droppedObjectData.initialDraggedData;
  // });

  // console.log("[onRefresh] initialDraggedData:", initialDraggedData);

  console.log("just checking the refresh event before the function invoking");
  useEffect(() => {
    // onRefresh logic, set up *once* in a useEffect
    if (window.widget) {
      const onRefresh = async () => {
      
        console.log("just checking the refresh event after the function invoking");
      };

      window.widget.addEvent("onRefresh", onRefresh);

      return () => {
        window.widget.removeEvent("onRefresh", onRefresh);
      };
    }
  }, []);

  return <>{children}</>;
};

export default WidgetLifecycle;
